*, *::before, *::after { box-sizing: border-box; }
* { margin: 0; }
input, button, textarea, select { font: inherit; }
/* imported google fonts examples */
/* .roboto { font-family: "Roboto"; font-weight: 400,500; font-style: normal; } */
/* variables---------------------------------------------------------------- */
/* tailwind CSS Lime palette */
:root{
  --bg950: hsl(13, 81%, 15%);
  --bg900: hsl(15, 75%, 28%);
  --bg800: hsl(15, 79%, 34%);
  --bg700: hsl(17, 88%, 40%);
  --bg600: hsl(21, 90%, 48%);
  --mg500: hsl(25, 95%, 53%);
  --fg400: hsl(27, 96%, 61%);
  --fg300: hsl(31, 97%, 72%);
  --fg200: hsl(32, 98%, 83%);
  --fg100: hsl(34, 100%, 92%);
  --fg50:  hsl(33, 100%, 96%);
}

/* main CSS */
body{
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 16px;
  background-color: var(--bg950);
  color: var(--fg50);
  header{
    display: flex;
    background-color: var(--bg700);
    margin: 0 0 1rem 0;
    #restaurantLogo {
      margin: 0.5rem auto 0.5rem 0.5rem;
    }
    nav{
      display: flex;
      gap: 1rem;
      margin: 0.25rem;
    }
  }
}
#interiorImageWrapper{
  display: flex;
  justify-content: center;
  padding: 1rem 0;
  div {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-size: contain;
    background-repeat: no-repeat;
    width: 80vw;
    height: 46vw; /*based off width to work like aspect ratio. overshoot a little*/
  }
